main {
  padding: 35px clamp(20px, 5vw, 40px);
  color: #1f2f56;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

main > h1 {
  text-align: center;
  font-size: clamp(23px, 5vw, 26px);
  font-weight: 900;
  color: #1f2f56;
}

main p {
  text-align: center;
  font-weight: 500;
  color: #7280a7;
  padding: 0 10px;
  line-height: 1.6;
}

.boldP {
  font-weight: 900;
  color: #1f2f56;
}

main a {
  color: #3829e0;
  font-weight: 900;
  font-size: 13px;
}

main a:hover {
  transition: 0.5s;
  color: #7476e2;
}

main > div {
  display: flex;
  background: #f5f7ff;
  border-radius: 10px;
  padding: clamp(12px, 5vw, 25px);
  justify-content: space-between;
  gap: 10px;
  align-items: center;
}

main > div > div {
  display: flex;
  align-items: center;
  gap: 15px;
}

main > button {
  width: 100%;
  padding: 15px;
  border-radius: 10px;
  border: none;
  outline: none;
  background-color: #3829e0;
  color: #fff;
  font-weight: 700;
  margin: 10px 0;
  box-shadow: 0px 15px 20px -10px #7280a7;
  cursor: pointer;
  transition: 0.5s;
}

main > button:hover {
  transition: 0.5s;
  background-color: #7476e2;
}

.cancel {
  color: #7280a7;
}

.cancel:hover {
  transition: 0.5s;
  color: #1f2f56;
  cursor: pointer;
}

@media (max-width: 350px) {
  main > div {
    flex-direction: column;
  }
}
